.buttonContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 20px;
}
@media (max-width: 1350px) {
  .buttonContainer {
    padding-right: calc(5px + 15 * (100vw - 320px) / 1030);
  }
}

.mainButton, .mainButtonDisabled {
  width: 345px;
  height: 68px;
  border-radius: 10px;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 0.4s, --ActiveColor2 0.4s;
}
@media (max-width: 1350px) {
  .mainButton, .mainButtonDisabled {
    width: calc(185px + 160 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .mainButton, .mainButtonDisabled {
    height: calc(52px + 16 * (100vw - 320px) / 1030);
  }
}
.mainButton span, .mainButtonDisabled span {
  font-size: 24px;
  color: white;
  font-weight: bold;
  letter-spacing: 1px;
}
@media (max-width: 1350px) {
  .mainButton span, .mainButtonDisabled span {
    font-size: calc(14px + 10 * (100vw - 320px) / 1030);
  }
}
@media (pointer: coarse) {
  .mainButton, .mainButtonDisabled {
    /* mobile device */
  }
  .mainButton:hover, .mainButtonDisabled:hover {
    transform: none;
  }
}
@media (pointer: fine), (pointer: none) {
  .mainButton, .mainButtonDisabled {
    /* desktop */
  }
  .mainButton:hover, .mainButtonDisabled:hover {
    transform: scale(1.05);
  }
}

.mainButton {
  --ActiveColor1: rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.mainButtonDisabled {
  --ActiveColor1: rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

.nextButton, .nextButtonDisabled {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 0.4s, --ActiveColor2 0.4s;
}
@media (max-width: 1350px) {
  .nextButton, .nextButtonDisabled {
    width: calc(50px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .nextButton, .nextButtonDisabled {
    height: calc(50px + 20 * (100vw - 320px) / 1030);
  }
}
.nextButton span, .nextButtonDisabled span {
  display: block;
  font-size: 24px;
  color: white;
  font-weight: bold;
  letter-spacing: 1px;
}
@media (max-width: 1350px) {
  .nextButton span, .nextButtonDisabled span {
    font-size: calc(14px + 10 * (100vw - 320px) / 1030);
  }
}
@media (pointer: coarse) {
  .nextButton, .nextButtonDisabled {
    /* mobile device */
  }
  .nextButton:hover, .nextButtonDisabled:hover {
    transform: none;
  }
}
@media (pointer: fine), (pointer: none) {
  .nextButton, .nextButtonDisabled {
    /* desktop */
  }
  .nextButton:hover, .nextButtonDisabled:hover {
    transform: scale(1.05);
  }
}

.nextButton {
  --ActiveColor1: rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.nextButtonDisabled {
  --ActiveColor1: rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

