@import './mixins/adaptiveMixin';

@property --ActiveColor1 {
  syntax: '<color>';
  initial-value: rgba(53,80,220,1);
  inherits: false;
}

@property --ActiveColor2 {
  syntax: '<color>';
  initial-value: rgba(39,233,247,1);
  inherits: false;
}

.app {
  height: 100vh;
  background: linear-gradient(140deg, rgba(53,80,220,1) 0%, rgba(39,233,247,1) 100%);
  display: flex;
  flex-direction: column;
  justify-content: end;
  font-family: Ubuntu sans-serif;
}
