@import "../../mixins/adaptiveMixin";

.quizContainer {
  font-size: 1.3rem;
  overflow-wrap: break-word;
}

.quizQuestion {
  @include adaptive('padding-bottom', 36, 26, 1);
}

.quizAnswers {
  display: flex;
  flex-direction: column;
  @include adaptive('gap', 20, 15, 1);
}

.quizAnswer {
  display: flex;
  @include adaptive('gap', 16, 11, 1);
  align-items: center;
}

.checkbox, .checkboxDisabled {
  @include adaptive("width", 60, 40, 1);
  @include adaptive("height", 60, 40, 1);
  border-radius:  50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 .4s, --ActiveColor2 .4s;
  color: white;
  font-size: 1.4rem;
}

.checkbox {
  --ActiveColor1:  rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.checkboxDisabled {
  --ActiveColor1:  rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

