.container, .containerDisabled {
  width: 97px;
  height: 97px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid transparent;
  transition: all 0.4s ease-in-out;
}
@media (max-width: 1350px) {
  .container, .containerDisabled {
    width: calc(47px + 50 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .container, .containerDisabled {
    height: calc(47px + 50 * (100vw - 320px) / 1030);
  }
}

.container {
  border-image-source: linear-gradient(140deg, rgb(53, 80, 220) 0%, rgb(39, 233, 247) 100%);
  border-image-slice: 1;
}

.containerDisabled {
  border-image-source: linear-gradient(140deg, rgb(212, 212, 212) 0%, rgb(212, 212, 212) 100%);
  border-image-slice: 1;
}

.quizNumber, .quizNumberDisabled {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 0.4s, --ActiveColor2 0.4s;
}
@media (max-width: 1350px) {
  .quizNumber, .quizNumberDisabled {
    width: calc(32px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .quizNumber, .quizNumberDisabled {
    height: calc(32px + 20 * (100vw - 320px) / 1030);
  }
}

.quizNumber {
  --ActiveColor1: rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.quizNumberDisabled {
  --ActiveColor1: rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

.quizNumberText {
  font-size: 1.3rem;
  color: white;
}

