@import '../../mixins/adaptiveMixin';

.container, .containerDisabled {
  @include adaptive("width", 97, 47, 1);
  @include adaptive("height", 97, 47, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid transparent;
  transition: all .4s ease-in-out;
}

.container {
  border-image-source: linear-gradient(140deg, rgba(53,80,220,1) 0%, rgba(39,233,247,1) 100%);
  border-image-slice: 1;
}

.containerDisabled {
  border-image-source: linear-gradient(140deg, rgba(212,212,212,1) 0%, rgba(212,212,212,1) 100%);
  border-image-slice: 1;
}

.quizNumber, .quizNumberDisabled {
  @include adaptive("width", 52, 32, 1);
  @include adaptive("height", 52, 32, 1);
  border-radius:  50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 .4s, --ActiveColor2 .4s;
}

.quizNumber {
  --ActiveColor1:  rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.quizNumberDisabled {
  --ActiveColor1:  rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

.quizNumberText {
  font-size: 1.3rem;
  color:  white;
}