.quiz {
  position: relative;
  height: 88%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 40px;
}
@media (max-width: 1350px) {
  .quiz {
    padding-top: calc(23px + 17 * (100vw - 320px) / 1030);
  }
}

.quizHeader {
  position: absolute;
  right: 0;
  left: 0;
  background: transparent;
  height: auto;
  padding-left: 80px;
  padding-right: 5px;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
  overflow-wrap: break-word;
}
@media (max-width: 1350px) {
  .quizHeader {
    padding-left: calc(64px + 16 * (100vw - 320px) / 1030);
  }
}

.quizWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 15px;
  overflow: hidden;
}

.quizContentWrapper {
  flex: 1 1 auto;
  display: flex;
  transition: transform 0.4s ease-in-out;
}

