.quizContainer {
  font-size: 1.3rem;
  overflow-wrap: break-word;
}

.quizQuestion {
  padding-bottom: 36px;
}
@media (max-width: 1350px) {
  .quizQuestion {
    padding-bottom: calc(26px + 10 * (100vw - 320px) / 1030);
  }
}

.quizAnswers {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 1350px) {
  .quizAnswers {
    gap: calc(15px + 5 * (100vw - 320px) / 1030);
  }
}

.quizAnswer {
  display: flex;
  gap: 16px;
  align-items: center;
}
@media (max-width: 1350px) {
  .quizAnswer {
    gap: calc(11px + 5 * (100vw - 320px) / 1030);
  }
}

.checkbox, .checkboxDisabled {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 0.4s, --ActiveColor2 0.4s;
  color: white;
  font-size: 1.4rem;
}
@media (max-width: 1350px) {
  .checkbox, .checkboxDisabled {
    width: calc(40px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .checkbox, .checkboxDisabled {
    height: calc(40px + 20 * (100vw - 320px) / 1030);
  }
}

.checkbox {
  --ActiveColor1: rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.checkboxDisabled {
  --ActiveColor1: rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

