@import "../../mixins/adaptiveMixin";

.buttonContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include adaptive("padding-right", 20, 5, 1);
}

.mainButton, .mainButtonDisabled {
  @include adaptive("width", 345, 185, 1);
  @include adaptive("height", 68, 52, 1);
  border-radius:  10px;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 .4s, --ActiveColor2 .4s;

  & span {
    @include adaptive("font-size", 24, 14, 1);
    color:  white;
    font-weight: bold;
    letter-spacing: 1px;
  }

  @media (pointer: coarse)  {
    /* mobile device */
    &:hover {
      transform: none;
    }
  }

  @media (pointer: fine), (pointer: none) {
    /* desktop */
    &:hover {
      transform: scale(1.05);
    }
  }
}

.mainButton {
  --ActiveColor1:  rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.mainButtonDisabled {
  --ActiveColor1:  rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}

.nextButton, .nextButtonDisabled {
  @include adaptive("width", 70, 50, 1);
  @include adaptive("height", 70, 50, 1);
  border-radius:  50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(140deg, var(--ActiveColor1) 0%, var(--ActiveColor2) 100%);
  transition: --ActiveColor1 .4s, --ActiveColor2 .4s;

  & span {
    display: block;
    @include adaptive("font-size", 24, 14, 1);
    color:  white;
    font-weight: bold;
    letter-spacing: 1px;
  }

  @media (pointer: coarse)  {
    /* mobile device */
    &:hover {
      transform: none;
    }
  }

  @media (pointer: fine), (pointer: none) {
    /* desktop */
    &:hover {
      transform: scale(1.05);
    }
  }
}

.nextButton {
  --ActiveColor1:  rgba(53,80,220,1);
  --ActiveColor2: rgba(39,233,247,1);
}

.nextButtonDisabled {
  --ActiveColor1:  rgba(212,212,212,1);
  --ActiveColor2: rgba(212,212,212,1);
}