@import '../../mixins/adaptiveMixin';

.quiz {
  position: relative;
  height: 88%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  padding-left: 5px;
  padding-right: 5px;
  @include adaptive('padding-top', 40, 23, 1);
}

.quizHeader {
  position: absolute;
  right: 0;
  left: 0;
  background: transparent;
  height: auto;
 @include adaptive("padding-left", 80, 64, 1);
  padding-right: 5px;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
  overflow-wrap: break-word;
}

.quizWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 15px;
  overflow: hidden;
}

.quizContentWrapper {
  flex: 1 1 auto;
  display: flex;
  transition: transform .4s ease-in-out;
}